              // this.picker.open();
              // console.log(`Before assigning ${this.startDateTime1}`);
              // this.startDateTime2 = this.startDateTime1;
              // this.startDateTime1 = new Date(this.startDateTime1);
              // console.log(`After assigning ${this.startDateTime1}`);
